@keyframes introLoad {
  0% {
    transform: translateY(-100px);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes introSlide {
  0% {
    transform: translateX(-100px);
  }
  50% {
    transform: translateX(-50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
body {
  background: linear-gradient(180deg, #af67e9, #6565e7);
  background-repeat: no-repeat;
  background-size: cover;
}

.heading {
  color: #1d1e35;
}

.question {
  color: #4a4b5e;
}
.question:hover {
  color: #f47c57;
}

.answer {
  color: #787887;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  overflow: hidden;
  display: grid;
  place-items: center;
}
@media only screen and (max-height: 640px) {
  body {
    overflow: visible;
  }
}
@media only screen and (max-height: 768px) {
  body {
    overflow: visible;
  }
}

.super-container {
  position: relative;
}
.super-container .box {
  display: none;
  animation: introSlide 1.5s forwards;
  opacity: 0;
}
@media only screen and (min-width: 1000px) {
  .super-container .box {
    display: grid;
    position: absolute;
    left: -8.9%;
    top: 41.6%;
    width: 8.8rem;
    z-index: 99;
  }
}
@media only screen and (min-width: 1100px) {
  .super-container .box {
    left: -8.7%;
    top: 41.2%;
    width: 9rem;
  }
}
@media only screen and (min-width: 1200px) {
  .super-container .box {
    left: -8.4%;
    top: 42.4%;
    width: 9.3rem;
  }
}
@media only screen and (min-width: 1300px) {
  .super-container .box {
    left: -7.3%;
    top: 42%;
    width: 9rem;
  }
}
@media only screen and (min-width: 1400px) {
  .super-container .box {
    top: 39.5%;
    left: -8.3%;
    width: 10.5rem;
  }
}

.container {
  position: relative;
  background-color: #ffffff;
  width: 26.5em;
  padding: 2em;
  border-radius: 20px;
  box-shadow: 11px 9px 27px -5px rgba(0, 0, 0, 0.48);
  transition: all 0.2s ease;
}
@media only screen and (min-width: 1000px) {
  .container {
    background: url("../images/bg-pattern-desktop.svg");
    background-size: 60rem;
    background-repeat: no-repeat;
    background-position: top -17rem left -40rem;
    background-color: #ffffff;
    overflow: hidden;
    width: 70vw;
    height: 71vh;
    display: grid;
    place-items: center;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (min-width: 1400px) {
  .container {
    width: 61.3375rem;
    height: 34.0792rem;
    background-position: top -16rem left -35rem;
  }
}

.hero-image-section-mobile img {
  width: 14rem;
}
.hero-image-section-mobile .illustration {
  position: relative;
  top: -7.8rem;
  left: 9%;
  z-index: 1;
  animation: introLoad 1s forwards;
  opacity: 0;
}
.hero-image-section-mobile .shadow {
  position: absolute;
  top: 0;
  left: 15%;
  animation: introLoad 1s forwards;
  opacity: 0;
}
@media only screen and (min-width: 1000px) {
  .hero-image-section-mobile {
    display: none;
  }
}

.hero-image-section-desktop {
  display: none;
}
@media only screen and (min-width: 1000px) {
  .hero-image-section-desktop {
    display: block;
    margin-top: 1rem;
  }
  .hero-image-section-desktop .illustration {
    left: -9%;
    position: absolute;
    top: 22%;
    width: 24rem;
    z-index: 1;
    animation: introSlide 1s forwards;
    opacity: 0;
  }
}
@media only screen and (min-width: 1000px) and (min-width: 1200px) {
  .hero-image-section-desktop .illustration {
    left: -8%;
    top: 22%;
    width: 25rem;
  }
}
@media only screen and (min-width: 1000px) and (min-width: 1400px) {
  .hero-image-section-desktop .illustration {
    left: -9%;
    top: 15%;
    width: 29rem;
  }
}

.hero-text-section {
  margin-bottom: 1rem;
}
@media only screen and (min-width: 1000px) {
  .hero-text-section {
    margin-right: 3.5rem;
    margin-bottom: -5rem;
  }
}

.accordion {
  height: 2.81rem;
  overflow: hidden;
  border-bottom: 1px solid #e7e7e9;
  transition: height 0.3s ease;
  cursor: pointer;
}
.accordion .arrow {
  transition: transform 0.4s ease;
}

.accordion-drop {
  height: 6rem;
}
.accordion-drop .question {
  color: #1d1e35;
  font-weight: 700;
}
.accordion-drop .arrow {
  transform: rotate(-180deg);
}

.question-holder {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.81rem;
  margin-bottom: 0.7rem;
}

body {
  font-family: "Kumbh Sans", sans-serif;
  font-size: 0.75em;
}

.heading {
  font-size: 2.5em;
  text-align: center;
  margin-top: -5rem;
  margin-bottom: 1rem;
}
@media only screen and (min-width: 1000px) {
  .heading {
    text-align: left;
  }
}
@media only screen and (min-width: 1400px) {
  .heading {
    font-size: 3em;
  }
}

.question {
  font-size: 1.12em;
}
@media only screen and (min-width: 1400px) {
  .question {
    font-size: 1.18em;
  }
}

.answer {
  font-size: 1em;
  width: 16.5rem;
  padding-right: 0.5rem;
}
@media only screen and (min-width: 1000px) {
  .answer {
    width: 17rem;
  }
}
@media only screen and (min-width: 1200px) {
  .answer {
    width: 20rem;
  }
}
@media only screen and (min-width: 1400px) {
  .answer {
    font-size: 1.1em;
  }
}